'use client';

import dynamic from 'next/dynamic';
import { LanguageProvider } from "./contexts/LanguageContext";
import { MinimalHeader } from "./components/MinimalHeader";
import { Hero } from "./components/Hero";
import { Trust } from "./components/Trust";
import "./loan.css";

// Lazy load heavy components
const HowItWorks = dynamic(() => import("./components/HowItWorks").then(mod => ({ default: mod.HowItWorks })), {
  loading: () => <div className="min-h-[400px]" />,
});
const LoanCalculator = dynamic(() => import("./components/LoanCalculator").then(mod => ({ default: mod.LoanCalculator })), {
  loading: () => <div className="min-h-[500px]" />,
});
const ApplicationForm = dynamic(() => import("./components/ApplicationForm").then(mod => ({ default: mod.ApplicationForm })), {
  loading: () => <div className="min-h-[400px]" />,
});
const Testimonials = dynamic(() => import("./components/Testimonials").then(mod => ({ default: mod.Testimonials })), {
  loading: () => <div className="min-h-[300px]" />,
});
const Contact = dynamic(() => import("./components/Contact").then(mod => ({ default: mod.Contact })), {
  loading: () => <div className="min-h-[400px]" />,
});
const Footer = dynamic(() => import("./components/Footer").then(mod => ({ default: mod.Footer })));
const SectionNavigator = dynamic(() => import("./components/SectionNavigator").then(mod => ({ default: mod.SectionNavigator })));

export default function LoanPage() {
  return (
    <LanguageProvider>
      <div className="min-h-screen bg-white">
        <MinimalHeader />
        <main>
          <section id="hero">
            <Hero />
          </section>
          <section id="trust">
            <Trust />
          </section>
          <section id="calculator">
            <LoanCalculator />
          </section>
          <section id="how-it-works">
            <HowItWorks />
          </section>
          <section id="application">
            <ApplicationForm />
          </section>
          <section id="testimonials">
            <Testimonials />
          </section>
          <section id="contact">
            <Contact />
          </section>
        </main>
        <Footer />
        <SectionNavigator />
      </div>
    </LanguageProvider>
  );
}
